import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import CountPage from '@/components/CountPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/CountPage',
      name: 'CountPage',
      component: CountPage
    }
  ]
})



